import "./styles.css";
var ball = document.getElementById("app");
var transform = "translate(20px, 0)";
var d = 0;
var s = 0;
window.addEventListener("keypress", function (event) {
  var key1 = event.keyCode;
  console.log(key1);
  var distance = ball.getBoundingClientRect();
  console.log(distance);
  console.log(window.outerWidth);

  if (key1 === 119) {
    if (distance.top <= 9) {
    } else {
      s = s - 20;
      transform = "translate(" + d + "px," + s + "px)";
      ball.style.transform = transform;
    }
  } else if (key1 === 97) {
    if (distance.left <= 9) {
    } else {
      d = d - 20;
      transform = "translate(" + d + "px," + s + "px)";
      ball.style.transform = transform;
    }
  } else if (key1 === 115) {
    if (distance.bottom >= window.innerHeight - 9) {
    } else {
      s = s + 20;
      transform = "translate(" + d + "px," + s + "px)";
      ball.style.transform = transform;
    }
  } else if (key1 === 100) {
    if (distance.right >= window.innerWidth - 9) {
    } else {
      d = d + 20;
      transform = "translate(" + d + "px," + s + "px)";
      ball.style.transform = transform;
    }
  }
});
